import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { VolunteeringPageComponent } from './components/volunteering-page/volunteering-page.component';
import { VolunteeringCardPageComponent } from './components/volunteering-card-page/volunteering-card-page.component';
import { GroupsResolver } from 'src/app/services/security/groups.resolver';

const routes: Routes = [
  {
    path: '',
    component: VolunteeringPageComponent,
    resolve: { groups: GroupsResolver },
    data: { animation: 'fadeAnimation', title: 'Волонтерство' }
  },
  {
    path: ':id',
    component: VolunteeringCardPageComponent,
    resolve: { groups: GroupsResolver },
    data: { animation: 'fadeAnimation', title: 'Волонтерство' }
  }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    providers: [GroupsResolver],
    exports: [RouterModule]
})
export class VolunteeringRoutingModule { }
